* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-titel {
  display: flex;
  text-align: center;
  gap: 10px;

  margin-bottom: 0;
  margin-top: 0;
}

.country-name {
  margin-bottom: 0;
  margin-top: 0;
}

.list {
  list-style: none;
}

.key {
  font-weight: 700;
}

.image {
  margin-bottom: none;
}